import React from "react";
import { Route, Switch } from "react-router-dom";
import Billing from "./Billing";
import Landing from "./Landing";

function App() {
  return (
    <Switch>
      <Route exact path="/">
        <Landing />
      </Route>
      <Route exact path="/billing">
        <Billing />
      </Route>
    </Switch>
  );
}

export default App;
